* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: sans-serif;
}

/* App */

.App {
  width: 50%;
  margin: 0 auto;
}

.App h1 {
  margin: 5px 0;
}

.App .seprator {
  width: 100%;
  margin: 5px 0;
}

@media (max-width: 720px) {
  .App {
    width: 90%;
  }
}

/* Form CSS */

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.form div {
  margin: 10px 0;
  width: 100%;
}

.form label {
  display: inline-block;
  font-size: 15px;
  margin: 3px 0;
}

.form input, .form textarea {
  width: 100%;
  border: 1px solid #f1f1f1;
  border-radius: 3px;
  background: #fefefe;
  padding: 5px;
}

.form textarea {
  resize: none;
}

.form .btn-container {
  display: flex;
  justify-content: flex-end;
}

.mn-btn {
  transition: 0.3s all;
  margin: 0 5px;
  padding: 5px 13px;
  background: #0088ff;
  color: white;
  border: none;
  border-radius: 3px;
}

.mn-btn:hover {
  background: #0067c2;
}

.mn-btn:active {
  background: #002f58;
}

/* List Css */

.noteList {
  width: 100%;
  color: #202020;
}

.noteMain {
  width: 100%;
  margin: 15px 0;
}

.noteList h3::after {
  content: '';
  display: block;
  height: 3px;
  width: 3rem;
  margin: 5px 0;
  background: #0088ff;
}

